@import url(https://fonts.googleapis.com/css?family=Lato:400,600,600i&display=swap);
.shop-page{width:79vw;margin:5vmax auto;justify-content:center}@media screen and (max-width: 813px){.shop-page{width:100vw}}.shop-page h1{font-family:'Ubuntu', sans-serif;color:#000000c0}

.collection-preview,.collection-page{display:flex;flex-direction:column;margin:0 auto 1vmax auto;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:-webkit-min-content;width:-moz-min-content;width:min-content}.collection-preview .title,.collection-page .title{font-size:2vmax;margin-bottom:1.5vmax}@media screen and (max-width: 813px){.collection-preview .title,.collection-page .title{font-size:5vmax}}.collection-preview .preview,.collection-page .preview{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}@media screen and (max-width: 813px){.collection-preview .preview,.collection-page .preview{grid-template-columns:1fr 1fr}}

.collection-item{display:block;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:12vmax;align-items:center;padding:.5vmax;-webkit-transition:all ease 1s;transition:all ease 1s;position:relative}@media screen and (max-width: 813px){.collection-item{width:40vw;height:-webkit-max-content;height:-moz-max-content;height:max-content;font-size:2vmax}}.collection-item .image{-webkit-transition:all ease .5s;transition:all ease .5s;background:no-repeat center 0;background-size:cover;height:15vmax;margin-bottom:1vh}@media screen and (max-width: 813px){.collection-item .image{height:25vmax}}.collection-item .custom-button{position:absolute;width:80%;opacity:.7;font-size:1em;top:13vmax;left:10%;display:none;-webkit-transition:all ease .4s;transition:all ease .4s}.collection-item .custom-button:hover{opacity:1}@media screen and (max-width: 813px){.collection-item .custom-button{font-size:.7em;position:relative;top:0;left:0;opacity:1;display:block}}.collection-item:hover .custom-button{display:block}.collection-item:hover .image{opacity:.8}.collection-item .collection-footer{width:100%;height:-webkit-min-content;height:-moz-min-content;height:min-content;display:flex;justify-content:space-between;font-size:1em}.collection-item .collection-footer .name{font-family:'Lato', sans-serif;width:90%;margin-bottom:15px}.collection-item .collection-footer .price{font-family:'Lato', sans-serif;font-weight:600;height:1.5vmax}

.collections-overview{display:flex;flex-direction:column}

.collection-page{display:flex;flex-direction:column;width:65vw}@media screen and (max-width: 813px){.collection-page{width:-webkit-max-content;width:-moz-max-content;width:max-content}}.collection-page .title{font-size:1.5vmax;margin:0 auto 1vmax}@media screen and (max-width: 813px){.collection-page .title{font-size:3vmax}}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-gap:0px}@media screen and (max-width: 813px){.collection-page .items{grid-template-columns:1fr 1fr}}.collection-page .items .collection-item{margin-bottom:1vmax}

